import React, { Component } from 'react';
import { database } from './firebase';

class App extends Component {
  constructor() {
    super();

    this.state = {
      messages: [],
    };

    this.onAddMessage = this.onAddMessage.bind(this);
  }

  componentDidMount() {
    this.scrollToBot();
  }

  componentWillMount() {
    const messagesRef = database.ref('messages')
      .orderByKey()
      .limitToLast(100);

    messagesRef.on('child_added', snapshot => {
      const message = { text: snapshot.val(), id: snapshot.key };

      this.setState(prevState => ({
        messages: [...prevState.messages, message],
      }));
    });

  }

  componentDidUpdate() {
    this.scrollToBot();
  }

  scrollToBot() {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });
  }

  onAddMessage(event) {
    event.preventDefault();
    database.ref('messages').push(this.input.value);
    this.input.value = '';
  }

  render() {
    return (
      <div className="App">
        <div className="chatroom">
        <h3>Ping's Chat App</h3>
          <ul className="chats" ref="chats">
            {this.state.messages.map(message =>
              <li className="chat right" key={message.id}>{message.text}</li>
            )}
            <div style={{ float: "left", clear: "both" }}
              ref={(el) => { this.messagesEnd = el; }}>
            </div>
          </ul>
          <form onSubmit={this.onAddMessage} className="input">
            <input autoFocus type="text" ref={node => this.input = node} />
            <input type="submit" />
          </form>
        </div>
      </div>
    );
  }
}

export default App;


